export default {
	name: 'VuePhotoSwipe',

	props: {
		galleryUID: {
			type: Number,
			default: 1
		},
		index: {
			type: Number,
			default: 0,
		},
		items: {
			type: Array,
			default: () => []
		}
	},
	
	mounted()
	{
		const options = {
			index: this.index,
			galleryUID: this.galleryUID
		};

		this.instance =  new PhotoSwipe(
			this.$refs.template,
			PhotoSwipeUI_Default,
			this.items,
			options
		);

		this.instance.init();

		this.instance.listen('close', () => {
			this.$emit('close');
		});
	}
}
